.body{
    margin: 0;
    padding: 0;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.container{
    width: 100%;
    height: 100%;
    display: flex;
}
.container ul{
    list-style: none;
    display: flex;
}
.container ul li a{
    padding: 10px;
    margin-right: 10px;
}
.hainavbar{
    background-color: rgb(63, 63, 63);
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 15px;
    padding: 15px;
    width: 100%;
    height: 80px;
    box-shadow: 0 7px 15px 0 rgba(0, 0, 0, 0,5);

}
.navbar{
    display: flex;
    flex-direction: row;
    font-size: 1.5em;
    padding: 15px;
}
.satu{
    color: crimson;
    font-weight: 700;
}
.dua{
    color: whitesmoke;
    font-weight: 700;
    padding-left: 8px;
}
.navigation{
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Poppins', sans-serif;
}
.navigation li{
    list-style: none;
    padding: 15px;
}
.navigation li a {
    color: whitesmoke;
    font-size: 20px;
    text-decoration: none;
    text-transform: capitalize;
}
.navigation li a:hover{
    color: crimson;
    transition: all .0,3s ease-in-out;
}
.navigition li img{
    width: 70px;
    height: 70px;
    border-radius: 70px;
}
.navigition li img:hover{
    width: 85px;
    height: 85px;
    border-radius: 85px;
}
