.social {
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1rem 0 3rem 0;
  }
  .social li {
    margin: 0 10px;
  }
  .social a {
    text-decoration: none;
    color: wheat;
  }
  
  .social li a {
    font-size: 1.1rem;
    transition: color 0.4s ease;
    justify-content: center;
    text-align: center;
    margin-right: 10px;
    position: relative;
    top: 10px;
    left: -10px;
  }
  .social li #fb:hover {
    color: rgb(0, 94, 255);
  }
  .social li #tw:hover {
    color: aqua;
  }
  .social li #tt:hover {
    color: black;
  }
  .social li #yt:hover {
    color: red;
  }
  .social li #ig:hover {
    color: rgb(253, 57, 90);
  }
  .social li #blog:hover {
    color: rgb(255, 153, 0);
  }