    width:1303px;
}
.skill-container{
    width: 100%;
    height: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: whitesmoke;
}
.skill-container h1{
    position: relative;
    top: 40px;
}
.skill-container .skill-wrapper{
    width: 100%;
    height: 359px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.skill-container .skill-wrapper .kotak{
    width: 80%;
    height: 50%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}
.skill-container .skill-wrapper .kotak .square{
    width: 400px;
    height: 100px;
    margin: 0px -80px;

    background-color: white;
    border-radius: 5px;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;
}
.skill-container .skill-wrapper .satu .square{
    margin-top: 40px;
}
.skill-container .skill-wrapper .dua .square{
    margin-bottom: 70px;
    margin-left: -88px;
}