body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  .containerFot {
    height: auto;
    margin-top: -17px;
    padding-top: 40px;
    font-family: Arial, Helvetica, sans-serif;
    color: wheat;
  }
  .footer-content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0, 5);
    border-bottom: solid 2px crimson;
  }
  .footer-content h3 {
    font-size: 1.6rem;
    font-weight: 500;
    text-transform: capitalize;
    line-height: 3rem;
  }
  .footer-content p {
    max-width: 500px;
    margin: 10px auto;
    line-height: 28px;
    font-size: 14px;
  }
  .footer-btn {
    background:  rgb(63, 63, 63);
    padding: 20px 0;
    text-align: center;
    margin-top: -70px;
    color: whitesmoke;
  }
  .footer-btn p {
    font-size: 14px;
    word-spacing: 2px;
    text-transform: capitalize;
  }